* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

#form {
  background-color: #baecdc;
  border-radius: 20px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 12px;
  width: 50%;
  height: 50%;
  padding: 20px;
  display: flex;
}

.inputs {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.inputs label {
  text-align: left;
  width: 100px;
}

.inputs input {
  background-color: #f5f5f5;
  border-radius: 12px;
  width: 300px;
  padding: 5px 10px;
  transition: all 2ms ease-in-out;
}

input:focus {
  transform: scaleX(1.2)scaleY(1.1);
}
